import React from 'react'
import { GoogleApiWrapper, Map, Marker } from 'google-maps-react';

export class MapContainer extends React.Component {

  render() {
    return (
      <div style={{ position: 'relative', height: 400, padding: 15, overflow: 'hidden' }}>
        <Map
          google={this.props.google}
          zoom={18}
          style={{
            height: 400
            , width: 'calc(100% - 15px)'
          }}
          initialCenter={{
            lat: -28.6640355,
            lng: -49.4305792
          }}
        >
          <Marker
            position={{ lat: -28.6640355, lng: -49.4305792 }} />
        </Map>
      </div>
    )
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyDwt_9QMkhbglFy9mxFfDBWR5qJpn3ESzQ"
})(MapContainer)